////// Packages //////////////////
import React, { useEffect, FC } from 'react';
import { Button, Spinner } from 'reactstrap';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { useDispatch, useSelector } from 'react-redux';

////// Local Components //////////
import rocket from 'assets/img/ui/rocket.png';

////// Custom Hooks //////////////
import useFetch from "hooks/useFetch";

////// Thunks ////////////////////
import { logout } from 'store/thunks/login'

interface Props {
  doLogout: boolean;
  titleTag: string;
}

const LogoutContent: FC<Props> = ({ doLogout = true, titleTag: TitleTag="h4" }) => {
  const loggedIn = useSelector( state => state.app.login.loggedIn );
  const { loading, error, post } = useFetch();

  const dispatch = useDispatch();

  useEffect(() => {
    if(doLogout) {
      dispatch(logout());
      /* post('/auth/logout', {})
        .then(data => {
          if (!data.isLoggedIn) {
            setLoggedOut(true);
          }
        }) */
    } 
  }, [])

  if ( !loggedIn ) {
    return (
      <>
        <img className="d-block mx-auto mb-4" src={rocket} alt="shield" width={70} />
        <TitleTag>See you again!</TitleTag>
        <p>
          Thanks for using Happin. You are <br className="d-none d-sm-block" />
          now successfully signed out.
        </p>
        <Button tag={Link} color="primary" size="sm" className="mt-3" to={`/auth/login`}>
          <FontAwesomeIcon icon="chevron-left" transform="shrink-4 down-1" className="mr-1" />
          Return to Login
        </Button>
      </>
    )
  } else {
    return (<Spinner color="primary" />)
  }
  
};


export default LogoutContent;
