////// Packages //////////////////
import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';
import { Button } from 'reactstrap';
////// Local Components //////////
import AuthLayout from './AuthLayout';

////// Global Components /////////
import LoginForm from 'compositions/auth/LoginForm';


////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////


const Login = () => {
  return (
    <AuthLayout
      leftSideContent={
        <Fragment>
          <p className="font-weight-bold">
            {`Don't have an account?`}
            <br />
            <Button className="mt-2" color="info" size="lg">
              <Link className="text-white" to="/auth/register">
                Get started!
              </Link>
            </Button>
          </p>
          <p className="mb-0 mt-4 mt-md-5 fs--1 font-weight-semi-bold text-300">
            Read our{' '}
            <Link className="text-underline text-300" to="#!">
              terms
            </Link>{' '}
            and{' '}
            <Link className="text-underline text-300" to="#!">
              conditions{' '}
            </Link>
          </p>
        </Fragment>
      }
    >
      <h3>Account Login</h3>
      <LoginForm layout="card" hasLabel />
    </AuthLayout>
  );
};

export default Login;
