////// Packages //////////////////
import React from 'react';
import { Link } from 'react-router-dom';

////// Local Components //////////
import AuthLayout from './AuthLayout';

////// Global Components /////////
import LogoutContent from 'compositions/auth/LogoutContent';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////


const Logout = () => {
  return (
    <AuthLayout
      leftSideContent={
        <p className="mb-0 mt-4 mt-md-5 fs--1 font-weight-semi-bold text-300">
          Read our{' '}
          <Link className="text-underline text-300" to="#!">
            terms
          </Link>{' '}
          and{' '}
          <Link className="text-underline text-300" to="#!">
            conditions{' '}
          </Link>
        </p>
      }
    >
      <div className="text-center">
        <LogoutContent titleTag="h3" doLogout/>
      </div>
    </AuthLayout>
  );
};

export default Logout;
