////// Packages //////////////////
import React, { FC } from 'react';
import PropTypes from 'prop-types';
import { Card, CardBody, Col, Row } from 'reactstrap';
import { useSelector } from 'react-redux';

////// Local Components //////////

////// Global Components /////////
import Background from 'components/Background';
import Flex from 'components/Flex';
import Section from 'components/Section';
import Logo from 'compositions/Logo'

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

///// Assets /////////
import bgShape from 'assets/img/backgrounds/bg-shape.png';
import shape1 from 'assets/img/backgrounds/shape-1.png';
import halfCircle from 'assets/img/backgrounds/half-circle.png';

interface Props {
  leftSideContent: React.ReactNode;
  children: React.ReactNode;
}

const AuthLayout: FC<Props> = ({ leftSideContent, children }) => {
  const isDark = useSelector( state => state.app.settings?.isDark );
  return (
    <Section fluid className="py-0">
      <Row noGutters className="min-vh-100 flex-center">
        <Col lg={8} className="col-xxl-5 py-3">
          <img className="bg-auth-circle-shape" src={bgShape} alt="" width="250" />
          <img className="bg-auth-circle-shape-2" src={shape1} alt="" width="150" />
          <Card className="overflow-hidden z-index-1">
            <CardBody className="p-0">
              <Row noGutters className="h-100">
                <Col md={5} className="text-white text-center bg-card-gradient order-1 order-md-0">
                  <div className="position-relative p-4 pt-md-5 pb-md-2">
                    <Background image={halfCircle} className="bg-auth-card-shape" />
                    <div className="z-index-1 position-relative">
                      <Logo className="text-white" pinStrokeRGB={{r: 255, g: 255, b: 255}} width={40} />
                      <h5 className={isDark ? 'text-800' : 'text-100'}>
                        Make it happin
                      </h5>
                      <br />
                      <p className={isDark ? 'text-800' : 'text-100'}>
                        Leave the information and data handling to us. 
                        We can do the heavy lifting of business tracking.
                        <br /><br />
                        With the power of happin, you can focus on getting work done.
                      </p>
                    </div>
                  </div>
                  <div className="mt-3 mb-4 mt-md-2 mb-md-4">{leftSideContent}</div>
                </Col>
                <Col md={7} tag={Flex} align="center" justify="center" className="order-0 order-md-1">
                  <div className="p-4 p-md-5 flex-grow-1">{children}</div>
                </Col>
              </Row>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </Section>
  );
};
AuthLayout.propTypes = {
  leftSideContent: PropTypes.node.isRequired,
  children: PropTypes.node.isRequired
};

export default AuthLayout;
