////// Packages //////////////////
import React, { useState, useEffect, FC } from 'react';
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';
import { Button, Form, FormGroup, Input, Row, Col } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

////// Local Components //////////
import withRedirect from '../../hoc/withRedirect';

////// Global Components /////////

////// CommonJS Helpers //////////
import { $api, $authentication } from 'environ';

////// Custom Hooks //////////////

////// Thunks ////////////////////

interface Props {
  setRedirect: (redirect: boolean)=>void;
  setRedirectUrl: (redirectUrl: string)=>void;
}

const ForgetPasswordForm: FC<Props> = ({ setRedirect, setRedirectUrl}) => {
  // State
  const [email, setEmail] = useState('');

  const handleSubmit = async () => {
    console.log('clicked!')
    const response = await fetch($authentication + '/request-password-reset',  {
      method: "POST",
      body: JSON.stringify({email}),
      credentials: "include",
      headers: {
        'Content-Type': 'application/json'
      }
    })
    const data = await response.json();
    if (data?.success && data?.resent) {
      toast.success(`An email is sent to ${email} with password reset link`);
      setRedirect(true);
    }
  };

  const handleEnter = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault()
    handleSubmit()
  }

  // Handler
  const handleButton = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.preventDefault()
    handleSubmit()
  }

  useEffect(() => {
    setRedirectUrl(`/auth/login`);
  }, [setRedirectUrl]);

  return (
        <>
        <Form className="mt-4" onSubmit={handleEnter}>
          <FormGroup>
            <Input
              className="form-control"
              placeholder="Email address"
              value={email}
              onChange={({ target }) => setEmail(target.value)}
              type="email"
            />
          </FormGroup>
          <FormGroup>
            <Button color="primary" block disabled={!email} onClick={handleButton}>
              Send reset link
            </Button>
          </FormGroup>
        
          <Link className="fs--1 text-600 ml-auto" to="#!">
            {`I can't recover my account using this page`}
            <span className="d-inline-block ml-1">&rarr;</span>
          </Link>
        </Form>
        <Col>
          <Row>
            <Link to="/auth/login" >
                <Button color="primary"  >
                  <FontAwesomeIcon icon="arrow-left" className="mr-1" />
                    Back to Login
                </Button>
              </Link>
          </Row>
        </Col>  
    </>
  );
};

export default withRedirect(ForgetPasswordForm);
