////// Packages //////////////////
import React, { useState, FC } from 'react';
import { toast } from 'react-toastify';
import { Button, Col, Form, Input, Row } from 'reactstrap';

////// Local Components //////////
import withRedirect from '../../hoc/withRedirect';


interface Props {
  setRedirect: (redirect: boolean)=>void;
  setRedirectUrl?: (redirectUrl: string)=>void;
}

const LockScreenForm: FC<Props> = ({ setRedirect, setRedirectUrl, ...rest }) => {
  // State
  const [password, setPassword] = useState('');

  // Handler
  const handleSubmit = event => {
    event.preventDefault();
    setRedirect(true);
    toast.success(`Logged in as Emma Watson`);
  };

  return (
    <Row tag={Form} noGutters onSubmit={handleSubmit} {...rest}>
      <Col>
        <Input
          className="mr-2"
          placeholder="Password"
          value={password}
          onChange={({ target }) => setPassword(target.value)}
          type="password"
        />
      </Col>
      <Col xs="auto" className="pl-2">
        <Button color="primary" disabled={!password}>
          Login
        </Button>
      </Col>
    </Row>
  );
};


export default withRedirect(LockScreenForm);
