////// Packages //////////////////
import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

////// Local Components //////////

////// Global Components /////////
import Login from 'layouts/auth/Login';
import Logout from 'layouts/auth/Logout';
import Registration from 'layouts/auth/Registration';
import ForgetPassword from 'layouts/auth/ForgetPassword';
import ConfirmMail from 'layouts/auth/ConfirmMail';
import PasswordReset from 'layouts/auth/PasswordReset';
import LockScreen from 'layouts/auth/LockScreen';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////


const AuthRoutes = ({ match: { url } }) => (
  <Switch>
    <Route path={`${url}/login`} exact component={Login} />
    <Route path={`${url}/logout`} exact component={Logout} />
    <Route path={`${url}/register`} exact component={Registration} />
    <Route path={`${url}/forgot-password`} exact component={ForgetPassword} />
    <Route path={`${url}/confirm-mail/:type/:email`} exact component={ConfirmMail} />
    <Route path={`${url}/password-reset/:emailURI/:jwt`} exact component={PasswordReset} />
    <Route path={`${url}/lock-screen`} exact component={LockScreen} />

    {/*Redirect*/}
    <Redirect to="/errors/404" />
  </Switch>
);

export default AuthRoutes;
