////// Packages //////////////////

import React from 'react'
import { Link, useParams } from 'react-router-dom';

////// Local Components //////////
import AuthLayout from './AuthLayout';

////// Global Components /////////
import ConfirmMailContent from 'compositions/auth/ConfirmMailContent';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////


const ConfirmMail = () => {
  const { email, type } = useParams<{email: string, type: string}>();

  return (
  <AuthLayout
    leftSideContent={
      <p className="mb-0 mt-4 mt-md-5 fs--1 font-weight-semi-bold text-300">
        Read our{' '}
        <Link className="text-underline text-300" to="#!">
          terms
        </Link>{' '}
        and{' '}
        <Link className="text-underline text-300" to="#!">
          conditions{' '}
        </Link>
      </p>
    }
  >
    <div className="text-center">
      <ConfirmMailContent layout="card" email={ decodeURIComponent(email) } type={ type } titleTag="h3" />
    </div>
  </AuthLayout>
  )
};

export default ConfirmMail;
