////// Packages //////////////////
import React from 'react';
import { Button } from 'reactstrap';
import { Link } from 'react-router-dom';

////// Local Components //////////
import AuthLayout from './AuthLayout';

////// Global Components /////////
import RegistrationForm from 'compositions/auth/RegistrationForm';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////


const Registration = () => (
  <AuthLayout
    leftSideContent={
      <p className="pt-3">
        Have an account?
        <br />
        <Button tag={Link} color="outline-light" className="mt-2 px-4" to="/authentication/card/login">
          Log In
        </Button>
      </p>
    }
  >
    <h3>Register Your Organization</h3>
    <RegistrationForm layout="card" hasLabel />
  </AuthLayout>
);

export default Registration;
