////// Packages //////////////////
import React from 'react';
import { Link, useParams } from 'react-router-dom';
import { useSelector } from 'react-redux';


////// Local Components //////////
import AuthLayout from './AuthLayout';

////// Global Components /////////
import PasswordResetForm from 'compositions/auth/PasswordResetForm';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////


const PasswordReset = () => {
  const { emailURI, jwt } = useParams<{emailURI: string, jwt: string}>();
  const email = decodeURIComponent(emailURI);

  const isDark = useSelector(state => state.app.settings?.isDark);

  return (
    <AuthLayout
      leftSideContent={
        <p className={`mb-0 mt-4 mt-md-5 fs--1 font-weight-semi-bold ${isDark ? 'text-800' : 'text-300'}`}>
          Read our{' '}
          <Link className={`text-underline ${isDark ? 'text-800' : 'text-300'}`} to="#!">
            terms
          </Link>{' '}
          and{' '}
          <Link className={`text-underline ${isDark ? 'text-800' : 'text-300'}`} to="#!">
            conditions{' '}
          </Link>
        </p>
      }
    >
      <h3>Reset password</h3>
      <PasswordResetForm layout="card" email={email} jwt={jwt} hasLabel />
    </AuthLayout>
  );
};

export default PasswordReset;
