////// Packages //////////////////
import React from 'react';
import { Col, Media, Row } from 'reactstrap';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';

////// Local Components //////////
import AuthLayout from './AuthLayout';
////// Global Components /////////
import LockScreenForm from 'compositions/auth/LockScreenForm';
import PersonAvatar from 'components/PersonAvatar';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////


const LockScreen = () => {
  const personId = useSelector( state => state.app.login.personId)

  return (
    <AuthLayout
      leftSideContent={
        <p className="mb-0 mt-4 mt-md-5 fs--1 font-weight-semi-bold text-300">
          Read our{' '}
          <Link className="text-underline text-300" to="#!">
            terms
          </Link>{' '}
          and{' '}
          <Link className="text-underline text-300" to="#!">
            conditions{' '}
          </Link>
        </p>
      }
    >
      <Row className="justify-content-center">
        <Col xs="auto">
          <Media className="align-items-center">
            <PersonAvatar ids={[personId]} size="4xl" className="mr-4" />
            <Media body>
              <h4>Hi! Emma</h4>
              <p className="mb-0">
                Enter your password <br />
                to access the admin.
              </p>
            </Media>
          </Media>
          <LockScreenForm className="mt-4" />
        </Col>
      </Row>
    </AuthLayout>
  );
};

export default LockScreen;
