////// Packages //////////////////
import React, { useState, useEffect, FC } from 'react';
import PropTypes from 'prop-types';
import { toast } from 'react-toastify';
import { Button, Form, FormGroup, Input } from 'reactstrap';
import Label from 'reactstrap/es/Label';
import classNames from 'classnames';

////// Local Components //////////
import withRedirect from 'hoc/withRedirect';
import SpinnerLogo from 'components/SpinnerLogo';

////// Custom Hooks //////////////
import useFetch from 'hooks/useFetch';

////// Thunks ////////////////////

////// Types /////////////////////
import { IEmail } from 'store/types/models/id';

interface Props {
  setRedirect: (redirect: boolean)=>void;
  setRedirectUrl: (redirectUrl: string)=>void;
  hasLabel: boolean;
  email: IEmail;
  jwt: string;
}

const PasswordResetForm: FC<Props> = ({ setRedirect, setRedirectUrl, hasLabel=false, email, jwt }) => {
  // State
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [isDisabled, setIsDisabled] = useState(true);
  const { post, loading } = useFetch();

  // Handler
  const handleSubmit = async e => {
    e.preventDefault();
    const res = await fetch('https://happin.design/authentication/reset-pass', {
        credentials: "include",
        method: "POST",
        body: JSON.stringify({ email, jwt, password }),
        headers: {
            "Content-Type": "application/json"
        }
    })
    const data = await res.json();
    if ( !data?.success ) return toast.warning('Password failed to reset. Please try again.')
    toast.success('Login with your new password');
    return setRedirect(true);
  };

  useEffect(() => {
    setRedirectUrl(`/auth/login`);
  }, [setRedirectUrl]);

  useEffect(() => {
    if (password === '' || confirmPassword === '') return setIsDisabled(true);

    setIsDisabled(password !== confirmPassword);
  }, [password, confirmPassword]);

  return (
    <Form className={classNames('mt-3', { 'text-left': hasLabel })} onSubmit={handleSubmit}>
      <FormGroup>
        {hasLabel && <Label>New Password</Label>}
        <Input
          placeholder={!hasLabel ? 'New Password' : ''}
          value={password}
          onChange={({ target }) => setPassword(target.value)}
          type="password"
        />
      </FormGroup>
      <FormGroup>
        {hasLabel && <Label>Confirm Password</Label>}
        <Input
          placeholder={!hasLabel ? 'Confirm Password' : ''}
          value={confirmPassword}
          onChange={({ target }) => setConfirmPassword(target.value)}
          type="password"
        />
      </FormGroup>
      { loading 
        ? 
        <SpinnerLogo width={50} /> 
        : 
        <Button color="primary" block className="mt-3" disabled={isDisabled}>
          Set password
        </Button>
      }
      
    </Form>
  );
};


export default withRedirect(PasswordResetForm);
