////// Packages //////////////////
import React, { Fragment, FC } from 'react';
import { Button } from 'reactstrap';
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
////// Local Components //////////

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import useFetch from 'hooks/useFetch';

////// Thunks ////////////////////

////// Assets //////////
import envelope from 'assets/img/ui/envelope.png';

interface Props {
  email: string;
  type?: any;
  titleTag?: any;
  layout?: string;
}

const ConfirmMailContent: FC<Props> = ({ email, layout="basic", type, titleTag: TitleTag="h4" }) => {
  const {post} = useFetch()

  const handleResend = (email: string, event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    event.preventDefault();
    post({email}, '/auth/resend')
      .then(data => {
        if (data?.resent) {
          toast.success('Verification email resent');
        }
      })
      .catch(err => console.log(err))
  };
  
  return (
  <Fragment>
    <img className="d-block mx-auto mb-4" src={envelope} alt="sent" width={70} />
    <TitleTag>Please check your email!</TitleTag>
    <p>
      An email has been sent to <strong>{email}</strong>. 
      Please click on the included link to 
      {type === 'validate' ? 
        ' validate your account' : 
        ' reset your password' 
      } .
    </p>
    <p>If you did not receive an email, please <Button className='btn btn-link' onClick={e => handleResend(email, e) }>click here</Button> to resend.</p>
    <Button tag={Link} color="primary" size="sm" className="mt-3" to={`/auth/login`}>
      <FontAwesomeIcon icon="chevron-left" transform="shrink-4 down-1" className="mr-1" />
      Return to login
    </Button>
  </Fragment>
  );
};


export default ConfirmMailContent;
